import React, { useState, useEffect, useCallback } from "react";

const numRows = 5;
const numCols = 5;
const tileColors = ["red", "blue", "green", "yellow", "purple"];

const generateBoard = () => {
  const board = [];
  for (let row = 0; row < numRows; row++) {
    const newRow = [];
    for (let col = 0; col < numCols; col++) {
      newRow.push(tileColors[Math.floor(Math.random() * tileColors.length)]);
    }
    board.push(newRow);
  }
  return board;
};

export const PuzzleApp = () => {
  const [board, setBoard] = useState(generateBoard());
  const [selectedTile, setSelectedTile] = useState(null);
  const [score, setScore] = useState(0);
  const [isInitialBoard, setIsInitialBoard] = useState(true);
  const [timer, setTimer] = useState(false);
  const [startGame, setStartGame] = useState(false);
  const [game, setGame] = useState(false);
  const [totalScore, setTotalScore] = useState(0);

  useEffect(() => {
    const timerId = setTimeout(() => {
      setTimer(true);
      setGame(false);
    }, 10000); // 30

    return () => clearTimeout(timerId); // timerId
  }, [game]);

  const handleStart = () => {
    setStartGame(true);
    setGame(true);
  };

  const handleRestart = () => {
    setTotalScore((prevTotalScore) => prevTotalScore + score);
    setTimer(false);
    setScore(0);
    setStartGame(true);
    setGame(true);
  };

  const swapTiles = (row1, col1, row2, col2) => {
    const newBoard = [...board];
    const temp = newBoard[row1][col1];
    newBoard[row1][col1] = newBoard[row2][col2];
    newBoard[row2][col2] = temp;
    setBoard(newBoard);
  };

  const handleTileClick = (row, col) => {
    setIsInitialBoard(false);
    if (selectedTile) {
      const [prevRow, prevCol] = selectedTile;
      if (
        (Math.abs(prevRow - row) === 1 && prevCol === col) ||
        (Math.abs(prevCol - col) === 1 && prevRow === row)
      ) {
        swapTiles(prevRow, prevCol, row, col);
      }
      setSelectedTile(null);
    } else {
      setSelectedTile([row, col]);
    }
  };

  const checkMatches = useCallback(() => {
    const newBoard = [...board];
    const toClear = new Set();
    let points = 0;

    // 45
    for (let row = 0; row < numRows; row++) {
      for (let col = 0; col < numCols - 3; col++) {
        if (
          newBoard[row][col] &&
          newBoard[row][col] === newBoard[row][col + 1] &&
          newBoard[row][col] === newBoard[row][col + 2] &&
          newBoard[row][col] === newBoard[row][col + 3]
        ) {
          toClear.add(`${row},${col}`);
          toClear.add(`${row},${col + 1}`);
          toClear.add(`${row},${col + 2}`);
          toClear.add(`${row},${col + 3}`);
          points += 5;
        }
      }
    }

    // 45
    for (let col = 0; col < numCols; col++) {
      for (let row = 0; row < numRows - 3; row++) {
        if (
          newBoard[row][col] &&
          newBoard[row][col] === newBoard[row + 1][col] &&
          newBoard[row][col] === newBoard[row + 2][col] &&
          newBoard[row][col] === newBoard[row + 3][col]
        ) {
          toClear.add(`${row},${col}`);
          toClear.add(`${row + 1},${col}`);
          toClear.add(`${row + 2},${col}`);
          toClear.add(`${row + 3},${col}`);
          points += 5;
        }
      }
    }

    // L6
    for (let row = 0; row < numRows - 2; row++) {
      for (let col = 0; col < numCols - 2; col++) {
        const color = newBoard[row][col];
        if (color) {
          // ""
          if (
            color === newBoard[row][col + 1] &&
            color === newBoard[row][col + 2] &&
            color === newBoard[row + 1][col]
          ) {
            toClear.add(`${row},${col}`);
            toClear.add(`${row},${col + 1}`);
            toClear.add(`${row},${col + 2}`);
            toClear.add(`${row + 1},${col}`);
            points += 6;
          }
          //  ""
          if (
            color === newBoard[row + 1][col] &&
            color === newBoard[row + 2][col] &&
            color === newBoard[row + 2][col + 1]
          ) {
            toClear.add(`${row},${col}`);
            toClear.add(`${row + 1},${col}`);
            toClear.add(`${row + 2},${col}`);
            toClear.add(`${row + 2},${col + 1}`);
            points += 6;
          }
          // ""
          if (
            color === newBoard[row + 1][col] &&
            color === newBoard[row + 2][col] &&
            color === newBoard[row][col + 1]
          ) {
            toClear.add(`${row},${col}`);
            toClear.add(`${row + 1},${col}`);
            toClear.add(`${row + 2},${col}`);
            toClear.add(`${row},${col + 1}`);
            points += 6;
          }
          //  ""
          if (
            color === newBoard[row][col + 1] &&
            color === newBoard[row][col + 2] &&
            color === newBoard[row + 1][col + 2]
          ) {
            toClear.add(`${row},${col}`);
            toClear.add(`${row},${col + 1}`);
            toClear.add(`${row},${col + 2}`);
            toClear.add(`${row + 1},${col + 2}`);
            points += 6;
          }
        }
      }
    }

    // 33
    for (let row = 0; row < numRows; row++) {
      for (let col = 0; col < numCols - 2; col++) {
        if (
          newBoard[row][col] &&
          newBoard[row][col] === newBoard[row][col + 1] &&
          newBoard[row][col] === newBoard[row][col + 2]
        ) {
          if (
            !toClear.has(`${row},${col}`) &&
            !toClear.has(`${row},${col + 1}`) &&
            !toClear.has(`${row},${col + 2}`)
          ) {
            toClear.add(`${row},${col}`);
            toClear.add(`${row},${col + 1}`);
            toClear.add(`${row},${col + 2}`);
            points += 3;
          }
        }
      }
    }

    // 33
    for (let col = 0; col < numCols; col++) {
      for (let row = 0; row < numRows - 2; row++) {
        if (
          newBoard[row][col] &&
          newBoard[row][col] === newBoard[row + 1][col] &&
          newBoard[row][col] === newBoard[row + 2][col]
        ) {
          if (
            !toClear.has(`${row},${col}`) &&
            !toClear.has(`${row + 1},${col}`) &&
            !toClear.has(`${row + 2},${col}`)
          ) {
            toClear.add(`${row},${col}`);
            toClear.add(`${row + 1},${col}`);
            toClear.add(`${row + 2},${col}`);
            points += 3;
          }
        }
      }
    }

    // 
    toClear.forEach((pos) => {
      const [r, c] = pos.split(",").map(Number);
      newBoard[r][c] = null;
    });
    if (!isInitialBoard) {
      setScore((prevScore) => prevScore + points);
    }
    setBoard(newBoard);
  }, [board, isInitialBoard]);

  const collapseBoard = useCallback(() => {
    const newBoard = [...board];
    for (let col = 0; col < numCols; col++) {
      let emptyRow = numRows - 1;
      for (let row = numRows - 1; row >= 0; row--) {
        if (newBoard[row][col] !== null) {
          newBoard[emptyRow][col] = newBoard[row][col];
          if (emptyRow !== row) newBoard[row][col] = null;
          emptyRow--;
        }
      }
      for (let row = emptyRow; row >= 0; row--) {
        newBoard[row][col] =
          tileColors[Math.floor(Math.random() * tileColors.length)];
      }
    }
    setBoard(newBoard);
  }, [board]);

  useEffect(() => {
    const timeout = setTimeout(() => {
      checkMatches();
      collapseBoard();
    }, 500);

    return () => clearTimeout(timeout);
  }, [board, checkMatches, collapseBoard]);

  return (
    <div className="PuzzleApp">
      <h1>Puzzle Game</h1>
      <p>{totalScore}</p>
      {startGame === true ? (
        <>
          {timer === true ? (
            <>
              <h1></h1>
              <p>{score}</p>
              <button
                onClick={() => {
                  handleRestart();
                }}
              >
                
              </button>
            </>
          ) : (
            <>
              <p>score: {score}</p>
              <div className="board">
                {board.map((row, rowIndex) => (
                  <div key={rowIndex} className="row">
                    {row.map((tile, colIndex) => (
                      <div
                        key={colIndex}
                        className={`tile ${tile} ${
                          selectedTile &&
                          selectedTile[0] === rowIndex &&
                          selectedTile[1] === colIndex
                            ? "selected"
                            : ""
                        }`}
                        onClick={() => handleTileClick(rowIndex, colIndex)}
                      ></div>
                    ))}
                  </div>
                ))}
              </div>
            </>
          )}
        </>
      ) : (
        <>
          <button
            onClick={() => {
              handleStart();
            }}
          >
            
          </button>
        </>
      )}
    </div>
  );
};
