import React, { useState } from "react";
import { Questions } from "./Questions";
import ReactPlayer from "react-player";
import { Spinner } from "@chakra-ui/react";

export const Question = () => {
  const [Score, setScore] = useState(0); //
  const [CurrentQuestion, setCurrentQuestion] = useState(0); //
  const [MissAnswer, setMissAnswer] = useState(false); //
  const [CorrectAnswer, setCorrectAnswer] = useState(false); //
  const [QuizEnd, setQuizEnd] = useState(false); //
  const [QuestionStart, setQuestionStart] = useState(true); //
  const [QuestionIndex, setQuestionIndex] = useState(null); //Questions
  const [QuizEndLoading, setQuizEndLoading] = useState(false); //
  const [UsedQuestions, setUsedQuestions] = useState([]); //
  const [answeredCorrectly, setAnsweredCorrectly] = useState(false); //
  const [answeredMiss, setAnsweredMiss] = useState(false); //

  const imageBaseUrl = process.env.PUBLIC_URL; //URL
  const audioBaseUrl = process.env.PUBLIC_URL; //URL

  //10
  const CheckAnswer = (isCorrect) => {
    if (isCorrect) {
      setScore((prev) => prev + 10); //10
      setCorrectAnswer(true); //
      setAnsweredCorrectly(true);
      setTimeout(() => {
        setCorrectAnswer(false);
        setAnsweredCorrectly(false);
        handleNextQuestion();
      }, 2000); // 2
    } else {
      setMissAnswer(true);
      setAnsweredMiss(true);
      setTimeout(() => {
        setMissAnswer(false);
        setAnsweredMiss(false);
        handleNextQuestion();
      }, 2000); // 2
    }
  };
  //
  const handleNextQuestion = () => {
    setMissAnswer(false);
    randomizeQuestion();
    if (CurrentQuestion < 9) {
      setCurrentQuestion((prev) => prev + 1);
    } else {
      setQuizEndLoading(true);
      setTimeout(() => {
        setQuizEndLoading(false);
        setQuizEnd(true);
      }, 2000);
    }
  };
  //
  const handleStart = () => {
    setQuestionStart(false);
    randomizeQuestion();
  };
  //
  const handleRestart = () => {
    setQuestionStart(true);
    setQuizEnd(false);
    setCurrentQuestion(0);
    setScore(0);
  };
  //
  const randomizeQuestion = () => {
    let randomIndex;
    do {
      randomIndex = Math.floor(Math.random() * Questions.length);
    } while (UsedQuestions.includes(randomIndex));

    setUsedQuestions((prev) => [...prev, randomIndex]);
    setQuestionIndex(randomIndex);
  };

  return (
    <div className="QuizAll">
      {answeredMiss && ( //
        <ReactPlayer
          url={`${audioBaseUrl}/Quiz-Wrong_Buzzer01-1.mp3`}
          playing={true}
          controls={true}
          volume={1}
          width="0"
          height="0"
        />
      )}
      {answeredCorrectly && ( //
        <ReactPlayer
          url="https://tadanote.tokyo/wp-content/uploads/2020/02/correct001.mp3"
          playing={true}
          controls={true}
          volume={1}
          width="0"
          height="0"
        />
      )}
      {QuestionStart ? ( //
        <div>
          <ReactPlayer
            url={`${audioBaseUrl}/Thunder-Real_Ambi03-1.mp3`}
            playing={true}
            controls={true}
            volume={1}
            width="0"
            height="0"
          />
          <h1>Quiz App</h1>
          <p></p>
          <p>10</p>
          <button onClick={handleStart}>Let's start!</button>
        </div>
      ) : QuizEndLoading ? ( //102
        <p>
          
          {<Spinner />}
        </p>
      ) : QuizEnd ? ( //
        <div>
          {Score >= 90 ? (
            <>
              <img
                src={`${imageBaseUrl}/mauro-shared-pictures-P-JuHm6FuMk-unsplash.jpg`}
                alt=""
                width="200"
                height="300"
              />
              <ReactPlayer
                url={`${audioBaseUrl}/Onoma-Sparkle01-2(Short).mp3`}
                playing={true}
                controls={true}
                volume={1}
                width="0"
                height="0"
              />
              <h6>{Score}</h6>
            </>
          ) : Score >= 70 ? (
            <>
              <ReactPlayer
                url={`${audioBaseUrl}/Onoma-Surprise01-1(Low).mp3`}
                playing={true}
                controls={true}
                volume={1}
                width="0"
                height="0"
              />
              <h6>{Score}</h6>
            </>
          ) : Score >= 50 ? (
            <>
              <h6>{Score}</h6>
              <ReactPlayer
                url={`${audioBaseUrl}/Onoma-Negative13-1(Low).mp3`}
                playing={true}
                controls={true}
                volume={1}
                width="0"
                height="0"
              />
            </>
          ) : (
            <>
              <img
                src={`${imageBaseUrl}/image_67170049.JPG`}
                alt=""
                width="200"
                height="300"
              />
              <h6></h6>
              <ReactPlayer
                url={`${audioBaseUrl}/Phrase04-1.mp3`}
                playing={true}
                controls={true}
                volume={1}
                width="0"
                height="0"
              />
            </>
          )}
          <button onClick={handleRestart}></button>
        </div>
      ) : MissAnswer ? ( //
        <>
          <img
            src={`${imageBaseUrl}//image_67170049.JPG`}
            alt=""
            width="200"
            height="300"
          />
          <h5></h5>
        </>
      ) : CorrectAnswer ? ( //
        <>
          <img
            src={`${imageBaseUrl}/mauro-shared-pictures-P-JuHm6FuMk-unsplash.jpg`}
            alt=""
            width="200"
            height="300"
          />
          <h2></h2>
        </>
      ) : (
        //
        <div>
          <ReactPlayer
            url={`${audioBaseUrl}/Timer02-2(Timbre1-30_Seconds).mp3`}
            playing={true}
            controls={true}
            volume={1}
            width="0"
            height="0"
          />
          <h3>Question No. {CurrentQuestion + 1}</h3>
          <h4>{Questions[QuestionIndex].question}</h4>
          {Questions[QuestionIndex].options.map((option, index) => (
            <button
              key={index}
              onClick={() =>
                CheckAnswer(option === Questions[QuestionIndex].answer)
              }
            >
              {option}
            </button>
          ))}
        </div>
      )}
    </div>
  );
};
